:root {
    --primary: #008080;
}

.btn {
    padding: 8px 20px;
    border-radius: 2px;
    outline: none;
    border: 5px #000;
    cursor: pointer;
}

.btn--primary{
    background-color: var(--primary);
    color: #fff;
    border: 1px solid var(--primary);
 
}

.btn--outline{
    background-color: transparent;
    color: #fff;
    padding: 8px 20px;
    border: 1px solid var(--primary);
    transition: all 0.3s ease-out;
}

.btn--medium{
    padding: 8px 20px;
    font-size: 20px;
}

.btn--large{
    padding: 12px 26px;
    font-size: 20px;
}

.btn--medium:hover, 
.btn--large:hover{
background: #fff;
color: #242424;
transition: all 0.3s ease-out;
}